.search-bar {
  width: 90%;
  max-width: 450px;
  height: 3em;
  display: flex;
  align-items: center;
  border-radius: 3em;
  padding: 1em 2em;
}

.search-bar input {
  background: transparent;
  flex: 1;
  border: 0;
  outline: none;
  padding: 1.5em 2em;
  font-size: 1em;
  color: #535353;
  transition: all 0.3s ease-in-out;
}
.search-bar input:focus {
  color: #000000;
}

.search-bar div {
  border: 0;
  width: 2em;
  margin-top: 1rem;
  background: transparent;
}
.search-bar div .icon {
  stroke: #41AA54;
}

@media screen and (max-width: 470px) {
  .search-bar button {
    display: none; /* Ocultar el botón */
  }
  .search-bar {
    padding: 0;
    min-width: 170px;
  }
}
.box-modal {
  position: fixed;
  top: 20%;
  left: 30%;
  transform: translate(-50%, -50%);
  background: white;
  padding: 2rem;
  z-index: 1001;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  width: 40rem;
}

.box-content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.close-button {
  align-self: flex-end;
  background: none;
  border: none;
  cursor: pointer;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.contentBox button {
  margin: 0 auto;
}

.buttonSidebar {
  background-color: #000000;
  border: none;
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
  margin: 2.5rem 0 2.5rem 0.5rem;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.buttonSidebar::before, .buttonSidebar::after {
  content: "";
  background-color: #FFFFFF;
  height: 0.2rem;
  width: 2rem;
  position: absolute;
  transition: all 0.3s ease;
}
.buttonSidebar::before {
  top: 1.3rem;
}
.buttonSidebar::after {
  top: 2rem;
}

.buttonSidebar.notClicked::before {
  top: 1.3rem;
  transform: rotate(0);
}
.buttonSidebar.notClicked::after {
  top: 2rem;
  transform: rotate(0);
}

.buttonSidebar.clicked::before {
  top: 1.6rem;
  transform: rotate(135deg);
}
.buttonSidebar.clicked::after {
  top: 1.6rem;
  transform: rotate(-135deg);
}

.seccion-nav {
  background-color: #000000;
  width: 5rem;
  height: 80vh;
  margin-top: 1rem;
  border-radius: 0 3rem 3rem 0;
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.seccion-sidebar-logo {
  width: 3rem;
}
