.sidebar {
  color: #FFFFFF;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #000000;
  padding: 4rem 0;
  position: absolute;
  top: 5rem;
  left: 0;
  transition: all 0.5s ease;
  border-radius: 0 3rem 3rem 0;
}
@media (max-width: 480px) and (max-height: 695px) {
  .sidebar {
    padding: 0;
  }
}

.sidebar.clicked {
  width: 16rem;
}

.sidebar.notClicked {
  width: 5rem;
}

.sidebar-item {
  width: 100%;
  padding: 1rem 0;
  color: #FFFFFF;
  cursor: pointer;
  margin: 3rem 0;
  display: flex;
  padding-left: 1rem;
}
@media (max-height: 790px) {
  .sidebar-item {
    margin: 2rem 0;
  }
}
@media (max-width: 480px) and (max-height: 695px) {
  .sidebar-item {
    margin: 2rem 0;
  }
}
.sidebar-item:hover {
  border-right: 0.4rem solid #FFFFFF;
}
.sidebar-item:hover .container-icon .notFull-icon {
  stroke: #FFFFFF;
}
.sidebar-item:hover .container-icon .full-icon {
  fill: #FFFFFF;
}
.sidebar-item .span {
  transition: all 0.3s ease;
  overflow: hidden;
}
.sidebar-item .span.clicked {
  width: 100%;
  padding-left: 2.5rem;
}
.sidebar-item .span.notClicked {
  width: 0%;
